import React, { useRef } from 'react';
import html2canvas from 'html2canvas';

export default function StageView({ stagedItems, setStagedItems }) {
  const chunkSize = 15;
  const chunks = [];
  for (let i = 0; i < stagedItems.length; i += chunkSize) {
    chunks.push(stagedItems.slice(i, i + chunkSize));
  }
  const tableRefs = useRef([]);

  function handleAskChange(idx, value) {
    const newItems = [...stagedItems];
    newItems[idx].askCount = parseInt(value, 10) || 0;
    setStagedItems(newItems);
  }

  function formatPrice(val) {
    const num = parseFloat(val);
    return isNaN(num) ? '' : `$${num.toFixed(2)}`;
  }

  function handleAskFocus(e) {
    if (e.target.value === "0") {
      e.target.value = "";
    }
  }

  async function downloadScreenshots() {
    for (let i = 0; i < tableRefs.current.length; i++) {
      const tableEl = tableRefs.current[i];
      if (tableEl) {
        const canvas = await html2canvas(tableEl, { backgroundColor: '#fff' });
        const imgData = canvas.toDataURL('image/png');
        const link = document.createElement('a');
        link.href = imgData;
        link.download = `stage_page_${i + 1}.png`;
        link.click();
      }
    }
  }

  return (
    <div className="table-responsive">
      <button className="btn btn-outline-dark mb-3" onClick={downloadScreenshots}>
        Download Screenshots (15 Per Image)
      </button>

      {chunks.map((chunk, pageIdx) => (
        <div key={pageIdx} style={{ marginBottom: 24, position: 'relative' }}>
          <table
            ref={el => tableRefs.current[pageIdx] = el}
            className="table table-bordered table-striped text-center align-middle"
            style={{
              width: '100%',
              fontWeight: 700,
              borderCollapse: 'collapse',
              tableLayout: 'fixed',
              background: '#fff'
            }}
          >
            <thead>
              <tr style={{ background: '#bababa' }}>
                <th style={{ width: '38%' }}>Product</th>
                <th style={{ width: '18%' }}>Price</th>
                <th style={{ width: '18%' }}>Qty</th>
                <th style={{ width: '18%' }}>Ask</th>
              </tr>
            </thead>
            <tbody>
              {chunk.map((item, idx) => (
                <tr key={idx}>
                  <td style={{
                    fontWeight: 700, textAlign: 'left', overflow: "hidden", textOverflow: "ellipsis", whiteSpace: "normal", wordBreak: "break-word"
                  }}>{item.product}</td>
                  <td style={{ fontWeight: 700 }}>{formatPrice(item.unitPrice)}</td>
                  <td style={{ fontWeight: 700 }}>{item.count || ''}</td>
                  <td style={{ fontWeight: 700 }}>
                    <input
                      type="number"
                      inputMode="numeric"
                      pattern="[0-9]*"
                      autoComplete="off"
                      className="form-control form-control-sm text-center"
                      style={{
                        maxWidth: 54,
                        fontWeight: 700,
                        border: '2px solid #000',
                        boxShadow: 'none'
                      }}
                      min="0"
                      value={item.askCount ? item.askCount : ""}
                      onChange={e => handleAskChange(
                        pageIdx * chunkSize + idx,
                        e.target.value
                      )}
                      onFocus={handleAskFocus}
                    />
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      ))}

      <style>
        {`
          .table-bordered th, .table-bordered td {
            border-width: 2px !important;
            border-color: #000 !important;
            font-weight: bold !important;
          }
        `}
      </style>
    </div>
  );
}
