import React, { useState, useEffect } from 'react';
import { Tabs, Tab } from 'react-bootstrap';
import Papa from 'papaparse';
import InventoryTable from './InventoryTable';
import StageView from './StageView';
import InvoiceView from './InvoiceView';
import { arrayToCsv } from './utils';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.bundle.min.js';

const basePath = process.env.NODE_ENV === 'production' ? process.env.PUBLIC_URL : '';
const APP_VERSION = "1.0.3";

// Load CSV every refresh (ignore localStorage)
function fetchCSV(url) {
  return fetch(url)
    .then(res => res.text())
    .then(text => {
      const result = Papa.parse(text, {
        header: true,
        skipEmptyLines: true,
        quoteChar: '"',
        escapeChar: '"',
      });
      return result.data.map(row => ({
        section: row.Section || '',
        product: row.Product || '',
        unitPrice: row['Unit Price'] ? String(row['Unit Price']).replace(/^\$/, '') : "0",
        count: parseInt(row.Count, 10) || 0,
        description: row.Description || '',
        selected: false,
        askCount: 0,
      }));
    })
    .catch(() => []);
}

export default function App() {
  const [inventory, setInventory] = useState([]);
  const [stagedItems, setStagedItems] = useState([]);
  const [discount, setDiscount] = useState(10);
  const [activeTab, setActiveTab] = useState('inventory');

  // Always load inventory from CSV after every refresh
  useEffect(() => {
    fetchCSV(`${basePath}/Inventory.csv`).then(data => setInventory(data));
  }, []);

  function generateStage() {
    const selected = inventory.filter(item => item.selected);
    setStagedItems(selected.map(item => ({ ...item, askCount: 0 })));
    setActiveTab('stage');
  }

  function resetSelected() {
    const newInv = inventory.map(item => ({ ...item, selected: false }));
    setInventory(newInv);
  }

  function exportInventory() {
    const csv = arrayToCsv(inventory);
    const blob = new Blob([csv], { type: 'text/csv;charset=utf-8' });
    const url = URL.createObjectURL(blob);
    const link = document.createElement('a');
    link.href = url;
    link.download = 'inventory.csv';
    document.body.appendChild(link);
    link.click();
    link.remove();
    URL.revokeObjectURL(url);
  }

  function resetInventory() {
    fetchCSV(`${basePath}/Inventory.csv`).then(data => setInventory(data));
  }

  function exportInvoice() {
    const filtered = stagedItems.filter(item => item.askCount > 0);
    const invoiceData = filtered.map(item => ({
      Section: item.section,
      Product: item.product,
      Description: item.description,
      Price: item.unitPrice,
      Quantity: item.askCount,
      Subtotal: (item.askCount * parseFloat(item.unitPrice)).toFixed(2),
    }));
    const csv = arrayToCsv(invoiceData);
    const blob = new Blob([csv], { type: 'text/csv;charset=utf-8' });
    const url = URL.createObjectURL(blob);
    const link = document.createElement('a');
    link.href = url;
    link.download = 'invoice.csv';
    document.body.appendChild(link);
    link.click();
    link.remove();
    URL.revokeObjectURL(url);
  }

  return (
    <div className="container my-4">
      <div className="mb-3 text-muted" style={{ fontSize: '0.9rem', fontWeight: 'bold' }}>
        App Version: v{APP_VERSION}
      </div>

      <h1 className="mb-4">Inventory Manager Web App</h1>

      <Tabs
        activeKey={activeTab}
        onSelect={k => setActiveTab(k)}
        className="mb-3 tab-container"
        variant="pills"
        fill
        justify
        mountOnEnter
        unmountOnExit
      >
        <Tab eventKey="inventory" title="Inventory">
          <div className="my-3 d-flex flex-wrap gap-2 justify-content-start">
            <button className="btn btn-primary" onClick={generateStage}>Generate Stage</button>
            <button className="btn btn-secondary" onClick={exportInventory}>Export Inventory CSV</button>
            <button className="btn btn-warning" onClick={resetInventory}>Reset Inventory From CSV</button>
            <button className="btn btn-info" onClick={resetSelected}>Reset Selected Items</button>
          </div>
          <InventoryTable inventory={inventory} setInventory={setInventory} />
        </Tab>
        <Tab eventKey="stage" title="Stage">
          <div className="my-3">
            <label htmlFor="discountInput" className="form-label">Discount (%)</label>
            <input
              id="discountInput"
              type="number"
              className="form-control form-control-sm"
              min="0"
              max="100"
              value={discount}
              onChange={e => setDiscount(e.target.value)}
              style={{ maxWidth: '140px' }}
              inputMode="decimal"
            />
          </div>
          <StageView stagedItems={stagedItems} setStagedItems={setStagedItems} />
        </Tab>
        <Tab eventKey="invoice" title="Invoice">
          <button className="btn btn-success mb-3" onClick={exportInvoice}>Export Invoice CSV</button>
          <InvoiceView stagedItems={stagedItems} discount={discount} />
        </Tab>
      </Tabs>
      <style>{`
        .tab-container {
          border-bottom: 1px solid #dee2e6;
          box-shadow: 0 2px 2px -2px rgba(0,0,0,.2);
          padding-bottom: 0.5rem;
          margin-bottom: 1rem;
        }
        .nav-pills .nav-link {
          border-radius: 1.25rem;
          font-weight: 500;
          padding: 0.5rem 1.2rem;
          margin-right: 0.5rem;
          color: #495057;
          transition: background-color 0.3s ease;
        }
        .nav-pills .nav-link.active {
          background-color: #0d6efd;
          color: white;
          box-shadow: 0 0 8px rgba(13,110,253,.4);
        }
        @media (max-width: 576px) {
          .nav-pills {
            flex-wrap: wrap;
          }
          .nav-pills .nav-link {
            flex: 1 0 33%;
            text-align: center;
            margin-bottom: 0.3rem;
          }
        }
      `}</style>
    </div>
  );
}
