import React, { useState } from 'react';

function EditableLabel({ value, onChange }) {
  const [editing, setEditing] = useState(false);
  const [tempValue, setTempValue] = useState(value);

  function startEdit() {
    setEditing(true);
  }

  function finishEdit() {
    setEditing(false);
    if (tempValue !== value) {
      onChange(tempValue);
    }
  }

  function onKeyDown(e) {
    if (e.key === 'Enter') {
      finishEdit();
    } else if (e.key === 'Escape') {
      setTempValue(value);
      setEditing(false);
    }
  }

  if (editing) {
    return (
      <input
        type="text"
        className="form-control form-control-sm"
        value={tempValue}
        autoFocus
        onChange={e => setTempValue(e.target.value)}
        onBlur={finishEdit}
        onKeyDown={onKeyDown}
      />
    );
  }

  return (
    <div
      tabIndex={0}
      role="textbox"
      className="editable-label"
      onClick={startEdit}
      onKeyDown={e => e.key === 'Enter' && startEdit()}
      style={{ cursor: 'pointer', minHeight: '2rem' }}
      aria-label="Edit product name"
    >
      {value}
    </div>
  );
}

export default function InventoryTable({ inventory, setInventory }) {
  const [expandedSections, setExpandedSections] = useState({});
  const [newSection, setNewSection] = useState('');
  const [newProduct, setNewProduct] = useState('');
  const [newPrice, setNewPrice] = useState('');
  const [newCount, setNewCount] = useState(0);

  const sections = inventory.reduce((acc, item) => {
    if (!acc[item.section]) acc[item.section] = [];
    acc[item.section].push(item);
    return acc;
  }, {});

  const sectionOrderSet = new Set();
  inventory.forEach(item => {
    if (!sectionOrderSet.has(item.section)) {
      sectionOrderSet.add(item.section);
    }
  });
  const sectionNames = Array.from(sectionOrderSet);

  function toggleSection(section) {
    setExpandedSections(prev => ({
      ...prev,
      [section]: !prev[section],
    }));
  }

  function toggleSelect(section, index) {
    const newInventory = [...inventory];
    const item = sections[section][index];
    const globalIndex = newInventory.findIndex(i => i === item);
    newInventory[globalIndex].selected = !newInventory[globalIndex].selected;
    setInventory(newInventory);
  }

  function handleProductChange(section, index, newProduct) {
    const newInventory = [...inventory];
    const item = sections[section][index];
    const globalIndex = newInventory.findIndex(i => i === item);
    newInventory[globalIndex].product = newProduct;
    setInventory(newInventory);
  }

  function handleChange(section, index, field, value) {
    const newInventory = [...inventory];
    const item = sections[section][index];
    const globalIndex = newInventory.findIndex(i => i === item);
    if (field === 'unitPrice') {
      newInventory[globalIndex][field] = value.replace(/[^0-9.]/g, '');
    } else {
      newInventory[globalIndex][field] = value;
    }
    setInventory(newInventory);
  }

  function removeItem(section, index) {
    const newInventory = [...inventory];
    const item = sections[section][index];
    const globalIndex = newInventory.findIndex(i => i === item);
    newInventory.splice(globalIndex, 1);
    setInventory(newInventory);
  }

  function addNewItem() {
    const sectionToAdd = newSection.trim() || (sectionNames.length ? sectionNames[0] : 'New Section');
    const productToAdd = newProduct.trim();
    const priceToAdd = newPrice.trim() === '' ? '0' : newPrice.trim().replace(/[^0-9.]/g, '');
    const countToAdd = isNaN(parseInt(newCount, 10)) ? 0 : parseInt(newCount, 10);

    if (!productToAdd) {
      alert('Please enter a product name to add.');
      return;
    }

    const newInventory = [...inventory];
    newInventory.push({
      section: sectionToAdd,
      product: productToAdd,
      unitPrice: priceToAdd,
      count: countToAdd,
      description: '',
      selected: false,
      askCount: 0,
    });

    // Sort inventory: by section order first, then product name (case-insensitive)
    newInventory.sort((a, b) => {
      const sectionAIdx = sectionNames.indexOf(a.section);
      const sectionBIdx = sectionNames.indexOf(b.section);
      if (sectionAIdx !== sectionBIdx) {
        return sectionAIdx - sectionBIdx;
      }
      return a.product.localeCompare(b.product, undefined, { sensitivity: 'base' });
    });

    setInventory(newInventory);
    setNewProduct('');
    setNewPrice('');
    setNewCount(0);
  }

  return (
    <div className="accordion" id="inventoryAccordion">
      <div className="mb-3 p-3 border rounded bg-light">
        <h5>Add New Item</h5>
        <div className="row g-2 align-items-center">
          <div className="col-12 col-md-3">
            <label htmlFor="newSection" className="form-label">Section</label>
            <select
              id="newSection"
              className="form-select form-select-sm"
              value={newSection || (sectionNames.length ? sectionNames[0] : '')}
              onChange={e => setNewSection(e.target.value)}
            >
              {sectionNames.map(section => (
                <option key={section} value={section}>{section}</option>
              ))}
              <option value="">New Section</option>
            </select>
          </div>
          <div className="col-12 col-md-3">
            <label htmlFor="newProduct" className="form-label">Product</label>
            <input
              id="newProduct"
              type="text"
              className="form-control form-control-sm"
              value={newProduct}
              onChange={e => setNewProduct(e.target.value)}
              placeholder="Product name"
            />
          </div>
          <div className="col-6 col-md-2">
            <label htmlFor="newPrice" className="form-label">Price ($)</label>
            <input
              id="newPrice"
              type="number"
              inputMode="decimal"
              pattern="[0-9.]*"
              className="form-control form-control-sm"
              min="0"
              step="0.01"
              value={newPrice}
              onChange={e => setNewPrice(e.target.value.replace(/[^0-9.]/g, ''))}
              placeholder="0.00"
            />
          </div>
          <div className="col-6 col-md-2">
            <label htmlFor="newCount" className="form-label">Count</label>
            <input
              id="newCount"
              type="number"
              inputMode="numeric"
              pattern="[0-9]*"
              className="form-control form-control-sm"
              min="0"
              value={newCount}
              onChange={e => setNewCount(e.target.value)}
              placeholder="0"
            />
          </div>
          <div className="col-12 col-md-2 d-flex align-items-end">
            <button className="btn btn-success w-100 btn-sm" onClick={addNewItem}>
              Add Item
            </button>
          </div>
        </div>
      </div>

      {Object.entries(sections).map(([section, items], index) => (
        <div className="accordion-item" key={section}>
          <h2 className="accordion-header" id={`heading${index}`}>
            <button
              className={`accordion-button ${!expandedSections[section] ? 'collapsed' : ''}`}
              type="button"
              onClick={() => toggleSection(section)}
              aria-expanded={expandedSections[section] ? 'true' : 'false'}
              aria-controls={`collapse${index}`}
            >
              {section}
            </button>
          </h2>
          <div
            id={`collapse${index}`}
            className={`accordion-collapse collapse ${expandedSections[section] ? 'show' : ''}`}
            aria-labelledby={`heading${index}`}
            data-bs-parent="#inventoryAccordion"
          >
            <div className="accordion-body p-0">
              <div className="table-responsive">
                <table className="table table-striped m-0">
                  <thead>
                    <tr>
                      <th>Select</th>
                      <th>Product</th>
                      <th>Price ($)</th>
                      <th>Quantity</th>
                      <th>Remove</th>
                    </tr>
                  </thead>
                  <tbody>
                    {items.map((item, idx) => {
                      const displayPrice =
                        item.unitPrice !== undefined && item.unitPrice !== null
                          ? String(item.unitPrice).replace(/[^0-9.]/g, '')
                          : '';
                      return (
                        <tr key={idx}>
                          <td className="align-middle">
                            <div className="form-check d-flex justify-content-center">
                              <input
                                className="form-check-input"
                                type="checkbox"
                                checked={item.selected || false}
                                onChange={() => toggleSelect(section, idx)}
                                id={`select-${section}-${idx}`}
                                style={{ minWidth: '24px', minHeight: '24px' }}
                              />
                              <label className="form-check-label visually-hidden" htmlFor={`select-${section}-${idx}`}>
                                Select item
                              </label>
                            </div>
                          </td>
                          <td>
                            <EditableLabel
                              value={item.product}
                              onChange={(val) => handleProductChange(section, idx, val)}
                            />
                          </td>
                          <td>
                            <input
                              className="form-control form-control-sm"
                              type="number"
                              inputMode="decimal"
                              pattern="[0-9.]*"
                              min="0"
                              step="0.01"
                              value={displayPrice}
                              onChange={e => handleChange(section, idx, 'unitPrice', e.target.value)}
                            />
                          </td>
                          <td>
                            <input
                              className="form-control form-control-sm"
                              type="number"
                              inputMode="numeric"
                              pattern="[0-9]*"
                              min="0"
                              value={item.count}
                              onChange={e => handleChange(section, idx, 'count', e.target.value)}
                            />
                          </td>
                          <td>
                            <button className="btn btn-danger btn-sm" onClick={() => removeItem(section, idx)}>
                              X
                            </button>
                          </td>
                        </tr>
                      );
                    })}
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      ))}
    </div>
  );
}
